.w-e-toolbar,
.w-e-text-container,
.w-e-menu-panel {
    padding: 0;
    margin: 0;
    box-sizing: border-box;

    * {
        padding: 0;
        margin: 0;
        box-sizing: border-box;
    }
}

.w-e-clear-fix:after {
    content: "";
    display: table;
    clear: both;
}
.w-e-toolbar{
  border-top-left-radius: 3px;
  border-top-right-radius: 3px;
  border:1px solid #f1f1f1;
}.w-e-toolbar .w-e-menu{
  display: inline-block;
 }
.w-e-text{
  overflow-y: auto !important;
}
.w-e-text-container{
  border-bottom-left-radius: 3px;
  border-bottom-right-radius: 3px;
}
.scalable-bar{
  height: 12px;

  background: #f1f1f1;
  border:1px solid #f1f1f1;
  position: absolute;
  bottom:0;
  width: 100%;
  text-align: center;
  font-size: 12px;
  color: #989898;
  line-height: 12px;
  &:hover{
    cursor: row-resize;
  }
}

.inline-input{
  display: inline-block;
  border:1px solid #ececec;
  border-radius: 3px;
  padding: 3px 15px;
}